import { Component } from '@angular/core';
import { IonicPage, LoadingController, ModalController, NavController, NavParams, ViewController } from 'ionic-angular';
import { ScheduleMapper } from '../../../models/schedule.model';
import { HospitalSearchPage, PatientEvolutionsPage } from '../../index';
import { EvolutionsProvider } from '../../../providers/evolutions/evolutions';
import { MedicalConductMapper } from '../../../models/medical-conduct.model';
import { PatientHouseholdMonitoringProvider } from '../../../providers/patient-household-monitoring/patient-household-monitoring';
import { HospitalConductEnum, MedicalConductEnum } from '../../../utils/enums';
import { HospitalModel } from '../../../models/hospital.model';
import { ToastProvider } from '../../../providers/toast/toast';
import { PatientMonitoringMapper } from '../../../models/patient-monitoring.model';
import { HospitalConductProvider } from '../../../providers/hospital-conduct/hospital-conduct';
import { HospitalConductMapper } from '../../../models/hospital-conduct.model';
import { HospitalDischargeMotiveProvider } from '../../../providers/hospital-discharge-motive/hospital-discharge-motive';
import { HospitalDischargeMotiveMapper } from '../../../models/hospital-discharge-motive.model';
import { AccommodationMapper } from '../../../models/accommodation.model';
import { AccommodationsProvider } from '../../../providers/accommodations/accommodations';
import { Patients } from '../../../providers/patients/patients';
import { UserMapper } from '../../../models/user.model';
import { User } from '../../../providers';

/**
 * Generated class for the PatientHospitalMonitoringPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-patient-hospital-monitoring',
  templateUrl: 'patient-hospital-monitoring.html',
})
export class PatientHospitalMonitoringPage {
  public monitoring: PatientMonitoringMapper;

  public schedule: ScheduleMapper;

  public patientMonitoring: any = {
    id: '',
    evolution: {id: '', description: '',},
    admitted_at: '',
    discharged_at: '',
    discharge_justification: '',
    other_conduct: '',
    hospital: {},
    available_days: 0,
    hospitalization_reason: '',
    hospital_discharge_motive_id: '',
    accommodation_id: '',
    hospital_conduct_id: '',
  };

  public hospitalConductList: MedicalConductMapper[];

  public hospitalDischargeMotives: HospitalDischargeMotiveMapper[];

  public accommodations: AccommodationMapper[];

  public hospital: HospitalModel;

  public medicalConductEnum = MedicalConductEnum;

  public hospitalConductEnum = HospitalConductEnum;

  public clinicSpecializationSelected: any[] = [];

  public clinicExamSelected: any[] = [];

  public loggedUser: UserMapper;

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private hospitalConductPrv: HospitalConductProvider,
              private hospitalDischargeMotivePrv: HospitalDischargeMotiveProvider,
              private modalCtrl: ModalController,
              private patientPrv: Patients, private userPrv: User,
              private toastPrv: ToastProvider, private evolutionsPrv: EvolutionsProvider,
              private accommodationPrv: AccommodationsProvider,
              private patientHouseholdMonitoringPrv: PatientHouseholdMonitoringProvider,
              public viewCtrl : ViewController,
              public loadingCtrl: LoadingController) {
    this.monitoring = this.navParams.get('monitoring');
    console.log('detail.monitoring', this.monitoring);
  }

  ionViewDidLoad() {
    this.getLoggedUser();
    console.log('ionViewDidLoad PatientHospitalMonitoringPage');
    for (let param in this.patientMonitoring) {
      // console.log('param', param, this.monitoring[param]);
      if (param !== 'evolution') {
        this.patientMonitoring[param] = this.monitoring[param] ? this.monitoring[param] : '';
      }
    }
    console.log('this.patientMonitoring', this.patientMonitoring);

    this.hospitalConductPrv.query().subscribe((hospitalConductList: HospitalConductMapper[]) => {
      this.hospitalConductList = hospitalConductList;
    });

    this.hospitalDischargeMotivePrv.query().subscribe((hospitalDischargeMotives: HospitalDischargeMotiveMapper[]) => {
      this.hospitalDischargeMotives = hospitalDischargeMotives;
    });

    this.accommodationPrv.query().subscribe((accommodations: AccommodationMapper[]) => {
      this.accommodations = accommodations;
    });
  }

  async getLoggedUser() {
    this.loggedUser = await this.userPrv.user();
    console.log('this.loggedUser', this.loggedUser);
  }

  async doPatientMonitoring() {

    if (!this.isValid()) {
      return;
    }

    let loading = this.loadingCtrl.create({
      content: 'Aguarde...'
    });

    loading.present();

    let patientMonitoringPayload = {
      id: this.patientMonitoring.id ? this.patientMonitoring.id : null,
      admitted_at: this.patientMonitoring.admitted_at,
      available_days: this.patientMonitoring.available_days,
      hospitalization_reason: this.patientMonitoring.hospitalization_reason,
      accommodation_id: this.patientMonitoring.accommodation_id,
      hospital_discharge_motive_id: this.patientMonitoring.hospital_discharge_motive_id,
      discharged_at: this.patientMonitoring.discharged_at,
      discharge_justification: this.patientMonitoring.discharge_justification,
      hospital_conduct_id: this.patientMonitoring.hospital_conduc_id
    };

    this.patientHouseholdMonitoringPrv.save(patientMonitoringPayload).subscribe((patientMonitoring) => {
      this.patientMonitoring.id = patientMonitoring.id;

      // Salva evolução do paciete
      let evolutionPayload = {
        id: this.patientMonitoring.evolution.id ? this.patientMonitoring.evolution.id : '',
        patient_monitoring_id: patientMonitoring.id ? patientMonitoring.id : null,
        description: this.patientMonitoring.evolution.description,
        patient_id: patientMonitoring.patient_id,
        professional_id: this.loggedUser.professional_id
      };
      this.evolutionsPrv.save(evolutionPayload).subscribe((evolution) => {
        this.patientMonitoring.evolution.id = evolution.id;

        loading.dismiss();

        this.toastPrv.show('Monitoramento hospitalar salvo com sucesso!');
        this.viewCtrl.dismiss({reload: true});
      }, (err) => {
        console.log('err', err);
        loading.dismiss();
      });
    }, (err) => {
        console.log('err', err);
        loading.dismiss();
    });
  }

  isValid(): boolean {

    if (!this.patientMonitoring.evolution.description) {
      this.toastPrv.show('Informe a evolução do paciente');
      return false;
    }

    // if (!this.patientMonitoring.medicalConducts.length) {
    //   this.toastPrv.show('Informe ao menos uma conduta médica');
    //   return false;
    // }
    //
    // if (this.patientMonitoring.medicalConducts.indexOf(this.medicalConductEnum.HOSPITAL) >= 0) {
    //   if (!this.hospital) {
    //     this.toastPrv.show('Selecione um hospital');
    //     return false;
    //   }
    //
    //   if (!this.patientMonitoring.hospitalization_reason) {
    //     this.toastPrv.show('Justifique a internação');
    //     return false;
    //   }
    // }
    //
    // if (this.patientMonitoring.medicalConducts.indexOf(this.medicalConductEnum.CLINIC) >= 0) {
    //   if (!this.clinicSpecializationSelected.length) {
    //     this.toastPrv.show('Selecione ao menos uma especialidade e clínica');
    //     return false;
    //   }
    // }
    //
    // if (this.patientMonitoring.medicalConducts.indexOf(this.medicalConductEnum.EXAM) >= 0) {
    //   if (!this.clinicExamSelected.length) {
    //     this.toastPrv.show('Selecione ao menos um exame e clínica');
    //     return false;
    //   }
    // }

    return true;
  }

  chooseHospital() {
    let hospitalSearchModal = this.modalCtrl.create(HospitalSearchPage);

    hospitalSearchModal.onDidDismiss((data: any) => {
      this.hospital = data.hospital;
      console.log('this.hospital', this.hospital);
    });

    hospitalSearchModal.present();
  }

  async evolutions() {
    let patient = await this.patientPrv.get(this.monitoring.patient_id).toPromise();
    console.log('patient', patient);
    this.navCtrl.push(PatientEvolutionsPage, {
      patient: patient
    });
  }

  dismiss() {
    this.viewCtrl.dismiss({reload: false});
  }

}



